#homebody {
    background-color: #4158D0;
    background-image: linear-gradient(180deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);


}

#homebody p {
    font-size: 1.2em;
}

.servicebox {
    background-color: #7e8386;
    background-image: linear-gradient(62deg, #7e8386 0%, #a18fb3 50%, #ad9494 100%);

}

#servicebackground {
    background-color: #434869;
    background-image: linear-gradient(180deg, #434869 0%, #693465 46%, #6f634c 100%);

}

#contactbody {
    background-color: #434869;
    background-image: linear-gradient(180deg, #434869 0%, #693465 46%, #6f634c 100%);

}

.active {
    border-bottom: #3552e2 solid !important;
}

#about {
    background-color: #434869;
    background-image: linear-gradient(180deg, #434869 0%, #693465 46%, #6f634c 100%);


}

#project {
    background-color: #434869;
    background-image: linear-gradient(180deg, #434869 0%, #693465 46%, #6f634c 100%);

}

.tab-content .current {
    display: block;
}

.tab-content {
    display: none;
}

#about1 {
    background-color: #434869;
    background-image: linear-gradient(180deg, #434869 0%, #693465 46%, #6f634c 100%);


}

a {
    text-decoration: none;
    color: white;
}

@media only screen and (max-width:500px) {

    .home,
    .about,
    .service,
    .project,
    .contact {
        display: grid !important;
    }
    .home div,
    .about div,
    .project div,
    .contact div {
        width: 80% !important;
    }
    .service div{
        width: 89% !important;
    }

    .img,
    .img img {
        margin-left: 2rem !important;
    }
    .App{
        width: 100% !important;
    }
}